.grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, 500px);
    justify-content: center;
    align-items: center;
}

.grid-item {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 500px;
}

.textPart {
    display: flex;
    flex-direction: column;
}

.topQuotemark {
    align-self: flex-start;
    font-size: 80px;
}

.bottomQuotemark {
    align-self: flex-end;
    font-size: 40px;
}

.quoteTxt {
    font-size: 23px;
}

@media only screen and (max-width: 1024px) {
    .topQuotemark {
        align-self: flex-start;
        font-size: 50px;
    }
    
    .bottomQuotemark {
        align-self: flex-end;
        font-size: 20px;
    }
    
    .quoteTxt {
        font-size: 10px;
        background-color: blue;
    }
}